.rotate {
  width: 110px;
}
.rotate > span {
  display: flex;
  transform: rotate(-90deg);
  justify-content: flex-start;
}
.rotate > span:before {
  content: '';
  padding-top: 100%;
  float: left;
}
#me .fp-tableCell {
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

#me .me-bottom {
  height: 90vh;
  width: 100%;
  margin-bottom: 50px;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

#me .me-bottom > p {
  padding: 15px 10px;
  font-size: medium;
  font-weight: 400;
  text-align: center;
  border-radius: 93px;
  background: #74c020;
  box-shadow: 20px 20px 60px #63a31b, -20px -20px 60px #85dd25;
}

#me .me-bottom > img {
  height: 10em;
  width: 10em;
  border-radius: 50%;
  background: #74c020;
  box-shadow: 20px 20px 60px #63a31b, -20px -20px 60px #85dd25;
}

#me .download-cv {
  border-radius: 93px;
  background: #74c020;
  box-shadow: 20px 20px 60px #63a31b, -20px -20px 60px #85dd25;
}

@media only screen and (min-width: 700px) {
  #me .me-bottom a {
    display: flex;
  }
  #me .me-bottom span {
    display: flex;
    transform: rotate(90deg);
    justify-content: flex-start;
  }
  #me .me-bottom > p {
    padding: 20px 10px;
    max-width: 1024px;
    font-size: larger;
    font-weight: 500;
    border-radius: 93px;
    color: white;
  }
  #me .me-bottom > img {
    border-radius: 50%;
    height: 20em;
    width: 20em;
  }
}
